@import '../index.css';

.posts {
	display: grid;
	grid-template-columns: 50% 50%;
	list-style: none;
	margin: 0;
	padding: 0;
}

.myWaste {
    padding: 60px;
    text-align: center;
    background: var(--special-bg-color);
    color: rgb(5, 5, 5);
    font-size: 75px;
}    

.category-grid {
	display: flex;
	align-items: center;
	justify-content: space-around;
	flex-direction: column;
}

.posts li {
	padding: 15px;
	margin: 50px;
	border-radius: 10px;
	background-color: var(--special-bg-color);
}